<template>
    <div class="datasetting">
        <yd-tab v-model="tab">
            <yd-tab-panel label="">
                <div class="datasetting-content">
                    <yd-cell-group>
                        <yd-cell-item>
                            <img slot="icon" src="../../static/image/man.png">
                            <span slot="left"></span>
                            <input slot="right" type="text" v-model="name" placeholder="">
                        </yd-cell-item>
                        <yd-cell-item>
                            <img slot="icon" src="../../static/image/calendar.png">
                            <span slot="left"></span>
                            <yd-datetime type="date" start-date="1970" :end-date="date" v-model="birthday" slot="right"></yd-datetime>
                        </yd-cell-item>
                        <yd-cell-item>
                            <img slot="icon" src="../../static/image/calendar.png">
                            <span slot="left"></span>
                            <input slot="right" type="text" @click="show = true" readonly v-model="sexName">
                            <input type="hidden" v-model="sexVal">
                        </yd-cell-item>
                    </yd-cell-group>
                </div>
            </yd-tab-panel>
            <yd-tab-panel label="">
                <yd-cell-group>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" type="password" v-model="pwd" placeholder=""></yd-input>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" type="password" v-model="newPwd" placeholder="" key="edit"></yd-input>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" type="password" v-model="rePwd" placeholder="" key="edit"></yd-input>
                    </yd-cell-item>
                </yd-cell-group>
            </yd-tab-panel>
            <yd-tab-panel label="">
                <yd-cell-group>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input type="text" slot="right" readonly :show-clear-icon="false" :show-success-icon="false" regex="mobile" v-model="mobile" placeholder=""></yd-input>
                        <yd-sendcode slot="right"
                                     storage-key="forgotPwd"
                                     v-model="sendDone"
                                     init-str=""
                                     @click.native="sendCode"
                                     type="primary"
                        ></yd-sendcode>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" v-model="smsCode" placeholder=""></yd-input>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" type="password" v-model="newPwd" placeholder="" key="back"></yd-input>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" type="password" v-model="rePwd" placeholder="" key="back"></yd-input>
                    </yd-cell-item>
                </yd-cell-group>
            </yd-tab-panel>
            <yd-actionsheet :items="sexItems" v-model="show" cancel=""></yd-actionsheet>
        </yd-tab>
        <!--  -->
        <yd-actionsheet :items="sexItems" v-model="show" cancel=""></yd-actionsheet>
        <yd-button-group>
            <yd-button size="large" bgcolor="#ff3b44" color="#fff" @click.native="submitHandler"></yd-button>
        </yd-button-group>
    </div>
</template>

<script>
export default {
    data () {
        return {
            tab: parseInt(this.GLOBAL.getStorage('settingTab')) || 0,
            name: '', // 
            birthday: '', // 
            sexName: '', // 
            sexVal: '', // val
            pwd: '', // 
            newPwd: '', // 
            rePwd: '', // 
            sendDone: false, // 
            smsCode: '', // 
            mobile: '', // 
            show: false,
            sexItems: [
                {
                    label: '',
                    val: 1,
                    callback: (e) => {
                        this.sexName = e.label
                        this.sexVal = e.val
                    }
                },
                {
                    label: '',
                    val: 2,
                    callback: (e) => {
                        this.sexName = e.label
                        this.sexVal = e.val
                    }
                },
                {
                    label: '',
                    val: 3,
                    callback: (e) => {
                        this.sexName = e.label
                        this.sexVal = e.val
                    }
                }
            ]
        }
    },
    computed: {
        // 
        date () {
            let date = new Date()
            let year = date.getFullYear()
            let month = date.getMonth() + 1
            let day = date.getDate()
            if (month < 10) {
                month = '0' + month
            }
            if (day < 10) {
                day = '0' + day
            }
            return year + '/' + month + '/' + day
        }
    },
    created () {
        // 
        this.$api.userInfo({}, res => {
            if (res.status) {
                this.name = res.data.nickname
                this.birthday = res.data.birthday
                this.mobile = res.data.mobile
                if (res.data.sex === 1) {
                    this.sexName = ''
                } else if (res.data.sex === 2) {
                    this.sexName = ''
                } else {
                    this.sexName = ''
                }
                this.sexVal = res.data.sex
            }
        })
    },
    methods: {
        sendCode () {
            this.$dialog.loading.open('...')
            setTimeout(() => {
                this.$dialog.loading.close()
                this.$api.sms({mobile: this.mobile, code: 'veri'}, res => {
                    if (res.status) {
                        this.sendDone = true
                        this.$dialog.toast({mes: res.msg, icon: 'success', timeout: 1500})
                    }
                })
            }, 1000)
        },
        // 
        submitHandler () {
            switch (this.tab) {
            case 0:
                this.$api.editInfo({
                    sex: this.sexVal,
                    birthday: this.birthday,
                    nickname: this.name
                }, res => {
                    this.$dialog.toast({mes: res.msg, timeout: 1000, icon: 'success'})
                })
                break
            case 1:
                if (this.pwd === '') {
                    this.$dialog.toast({mes: '', timeout: 1000})
                } else if (this.newPwd === '') {
                    this.$dialog.toast({mes: '', timeout: 1000})
                } else if (this.rePwd === '') {
                    this.$dialog.toast({mes: '', timeout: 1000})
                } else {
                    this.$api.editPwd({
                        pwd: this.pwd,
                        newpwd: this.newPwd,
                        repwd: this.rePwd
                    }, res => {
                        this.resultStatus(res)
                    })
                }
                break
            case 2:
                if (this.newPwd === '') {
                    this.$dialog.toast({mes: '', timeout: 1000})
                } else if (this.rePwd === '') {
                    this.$dialog.toast({mes: '', timeout: 1000})
                } else if (this.smsCode === '') {
                    this.$dialog.toast({mes: '', timeout: 1000})
                } else {
                    this.$api.forgotPwd({
                        mobile: this.mobile,
                        code: this.smsCode,
                        newpwd: this.newPwd,
                        repwd: this.rePwd
                    }, res => {
                        this.resultStatus(res)
                    })
                }
                break
            default:
                break
            }
        },
        //   
        resultStatus (res) {
            if (res.status) {
                this.$dialog.alert({
                    mes: res.msg + '',
                    timeout: 1000,
                    callback: () => {
                        this.GLOBAL.removeStorage('settingTab')
                        this.GLOBAL.removeStorage('user_token')
                        this.$router.push({path: '/login'})
                    }
                })
            }
        }
    },
    watch: {
        tab () {
            this.GLOBAL.setStorage('settingTab', this.tab)
            // tab
            this.pwd = ''
            this.newPwd = ''
            this.rePwd = ''
        }
    }
}
</script>



