<template>
    <div class="withdrawcash">
        <div class="withdrawcash-top" @click="showHandler" v-if="card.id">
            <img class="banklogo" :src="card.bank_logo"/>
            <div class="bankcard">
                <span>{{ card.bank_name }}</span>
                <p>{{ card.card_number }} {{ card.card_type }}</p>
            </div>
            <img src="../../static/image/right.png" class="right-img"/>
        </div>
        <div class="withdrawcash-top" v-else>
            <div style="text-align: center">
                <yd-button size="small" type="hollow" shape="circle" @click.native="showHandler"></yd-button>
            </div>
        </div>
        <div class="withdrawcash-mid">
            <p></p>
            <div class="withdrawcash-input">
                <span></span><input type="number" v-model="money"/>
            </div>
            <p v-show="!showError"> {{ available }} </p>
            <p v-show="showError" style="color: #f00;"></p>
        </div>
        <div class="withdrawcash-bottom">
            <div style="margin: 15px">
                <yd-button size="large" type="danger" v-show="isSubmit" @click.native="submitHandler"></yd-button>
                <yd-button size="large" type="disabled" disabled v-show="!isSubmit"></yd-button>
            </div>
        </div>
        <!--  -->
        <yd-popup v-model="showWindow" position="bottom" width="20%" height="60%">
            <div class="cardlist">
                <div v-if="cardList.length">
                    <div class="cardlist-item" v-for="(item, index) in cardList" :key="index" @click="selectHandler(index)">
                        <img class="banklogo" :src="item.bank_logo"/>
                        <div class="bankcard">
                            <yd-badge shape="square" type="primary" v-if="item.is_default === 1"></yd-badge>
                            <span>{{ item.bank_name }}</span>
                            <p>{{ item.card_number }} {{ item.card_type }}</p>
                        </div>
                    </div>
                </div>
                <div v-else style="text-align: center">
                    
                </div>
                <div style="margin: 15px">
                    <yd-button size="large" type="danger" @click.native="addCard"></yd-button>
                </div>
            </div>
        </yd-popup>
    </div>
</template>

<script>
export default {
    data () {
        return {
            card: [], // 
            cardList: [], // 
            money: '', // 
            showWindow: false, // 
            showError: false, //  
            isSubmit: false, // 
            available: '' //  
        }
    },
    mounted () {
        this.defaultCard()
        this.getBalance()
    },
    methods: {
        // 
        defaultCard () {
            this.$api.getDefaultBankCard({}, res => {
                this.card = res.data
            })
        },
        // 
        getBalance () {
            this.$api.userInfo({}, res => {
                this.available = res.data.balance || 0
            })
        },
        //  
        submitHandler () {
            if (!this.card) {
                this.$dialog.toast({mes: '', timeout: 1000})
            } else if (!this.money) {
                this.$dialog.toast({mes: '', timeout: 1000})
            } else if (Number(this.money) === 0) {
                this.$dialog.toast({mes: '0', timeout: 1000})
            } else {
                this.$api.userToCash({
                    money: this.money,
                    cardId: this.card.id
                }, res => {
                    if (res.status) {
                        this.$router.go(-1)
                        this.$dialog.toast({mes: '', timout: 1000})
                    }
                })
            }
        },
        // 
        showHandler () {
            this.showWindow = true
            this.bankCardList()
        },
        // 
        bankCardList () {
            this.$api.getBankCardList({}, res => {
                this.cardList = res.data
            })
        },
        // 
        selectHandler (index) {
            this.showWindow = false
            this.card = this.cardList[index]
        },
        // 
        addCard () {
            this.$router.push({path: '/bankcard'})
        }
    },
    watch: {
        //  
        money () {
            if (this.money === '') {
                this.isSubmit = false
            } else {
                this.isSubmit = true
            }
            //  
            if (Number(this.money) > Number(this.available)) {
                this.showError = true
                this.isSubmit = false
            } else {
                this.showError = false
            }
        }
    }
}
</script>

<style>
    .withdrawcash-top{
        background-color: #fff;
        text-align: left;
        padding: .2rem;
        position: relative;
        margin-top: .2rem;
    }
    .withdrawcash .banklogo{
        width: .6rem;
        height: .6rem;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .bankcard{
        display: inline-block;
        margin-left: .7rem;
    }
    .bankcard p{
        margin-top: .1rem;
        color: #999;
    }
    .withdrawcash-mid{
        text-align: left;
        margin-top: .2rem;
        padding: .2rem;
        background-color: #fff;
    }
    .withdrawcash-mid p{
        margin-bottom: .1rem;
        color: #666;
    }
    .withdrawcash-input{
        font-size: .8rem;
        position: relative;
    }
    .withdrawcash-input span{
        position: absolute;
        font-size: .7rem;
        top: 50%;
        transform: translateY(-50%);
    }
    .withdrawcash-input input{
        border: none;
        border-bottom: 1px solid #e5e5e5;
        padding: 0.05rem 0 .1rem .8rem;
        width: 100%;
    }
    .withdrawcash-bottom .yd-btn-block{
        height: .8rem;
        font-size: .3rem;
    }
    .cardlist{
        text-align: left;
    }
    .cardlist .cardlist-item{
        background-color: #fff;
        padding: .2rem;
        position: relative;
        border-bottom: 1px solid #e5e5e5;
    }
    .cardlist .banklogo{
        width: .6rem;
        height: .6rem;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .bankcard{
        display: inline-block;
        margin-left: .7rem;
    }
    .bankcard p{
        margin-top: .1rem;
        color: #999;
    }
    .card-select{
        width: .3rem;
        height: .3rem;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: .2rem;
    }
    .cardlist .yd-btn-block{
        height: .8rem;
        font-size: .3rem;
    }
</style>



