<template>
    <div class="orderdetailheader">
        <div class="" v-if="status === 'pending_payment'">
            <h3></h3>
            <p> {{ remaining }} </p>
        </div>
        <div class="" v-else-if="status === 'pending_delivery'">
            <h3></h3>
            <p>,</p>
        </div>
        <div class="" v-else-if="status === 'pending_receipt'">
            <h3></h3>
            <p>: {{ delivery[0].logi_name }}</p>
            <p>: {{ delivery[0].logi_no }}</p>
        </div>
        <div class="" v-else-if="status === 'pending_evaluate'">
            <h3></h3>
            <p> {{ remaining }} </p>
        </div>
        <div class="" v-else-if="status === 'completed'">
            <h3></h3>
            <p></p>
        </div>
        <div class="" v-else-if="status === 'cancel'">
            <h3></h3>
            <p></p>
        </div>
        <div class="" v-else>
            <h3></h3>
            <p></p>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        // 
        status: {
            type: [String, Array],
            default () {
                return []
            }
        },
        // 
        remaining: {
            type: [String, Array, Boolean],
            default () {
                return []
            }
        },
        // 
        delivery: {
            type: [Array, Object],
            default () {
                return []
            }
        }
    }
}
</script>

<style>
    .orderdetailheader{
        width: 100%;
        height: 2.3rem;
        background: url(../../static/image/backimg.png) no-repeat;
        background-size: 100% 100%;
    }
    .orderdetailheader>div{
        width: 70%;
        margin: 0 auto;
        text-align: left;
        color: #fff;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }
    .orderdetailheader h3{
        font-size: .3rem;
        margin-bottom: .2rem;
    }
</style>



