<template>
    <div class="orderdetailfooter" v-if="status === 'pending_payment'">
        <yd-button type="hollow" shape="circle" class="left-btn" @click.native="cancel"></yd-button>
        <yd-button type="hollow" shape="circle" class="right-btn" @click.native="pay"></yd-button>
    </div>
    <div class="orderdetailfooter" v-else-if="status === 'pending_delivery'">
        <yd-button type="hollow" shape="circle" class="right-btn" @click.native="afterSales"></yd-button>
    </div>
    <div class="orderdetailfooter" v-else-if="status === 'pending_receipt'">
        <yd-button type="hollow" shape="circle" class="left-btn" @click.native="afterSales"></yd-button>
        <yd-button type="hollow" shape="circle" class="right-btn" @click.native="logistics"></yd-button>
        <yd-button type="hollow" shape="circle" class="right-btn" @click.native="confirm"></yd-button>
    </div>
    <div class="orderdetailfooter" v-else-if="status === 'pending_evaluate'">
        <yd-button type="hollow" shape="circle" class="left-btn" @click.native="afterSales"></yd-button>
        <yd-button type="hollow" shape="circle" class="right-btn" @click.native="evaluate"></yd-button>
    </div>
    <div class="orderdetailfooter" v-else-if="status === 'completed' || status === 'cancel'">
    </div>
    <div class="orderdetailfooter" v-else>
        <yd-button type="hollow" shape="circle" class="left-btn" @click.native="afterSales"></yd-button>
    </div>
</template>

<script>
export default {
    props: {
        // 
        status: {
            type: [Array, String],
            default () {
                return []
            }
        }
    },
    methods: {
        // 
        cancel () {
            this.$emit('cancel')
        },
        // 
        confirm () {
            this.$emit('confirm')
        },
        // 
        afterSales () {
            this.$emit('afterSales')
        },
        pay () {
            this.$emit('pay')
        },
        // 
        evaluate () {
            this.$emit('evaluate')
        }

    }
}
</script>

<style>
</style>



