<template>
    <div class="">
        <yd-cell-group>
            <yd-cell-item href="/mybankcardlist" type="link">
                <img slot="icon" src="../../static/image/bankcard.png">
                <span slot="left"></span>
            </yd-cell-item>
            <yd-cell-item href="/recommendlist" type="link">
                <img slot="icon" src="../../static/image/coupon.png">
                <span slot="left"></span>
            </yd-cell-item>
            <yd-cell-item href="/datasetting" type="link">
                <img slot="icon" src="../../static/image/set.png">
                <span slot="left"></span>
            </yd-cell-item>
        </yd-cell-group>
        <div style="margin: 15px">
            <yd-button size="large" type="danger" @click.native="logout"></yd-button>
        </div>
    </div>
</template>

<script>
export default {
    methods: {
        logout () {
            this.$dialog.confirm({
                mes: '?',
                opts: [
                    {
                        txt: '',
                        color: false
                    },
                    {
                        txt: '',
                        color: true,
                        callback: () => {
                            this.$api.logout({}, res => {
                                if (res.status) {
                                    this.GLOBAL.removeStorage('user_token')
                                    this.GLOBAL.removeStorage('login-type')
                                    this.$router.replace('/login')
                                }
                            })
                        }
                    }
                ]
            })
        }
    }
}
</script>

<style scoped>

</style>



