// api
const ApiUrl = () => {
    let apiUrl
    if (process.env.NODE_ENV === 'development') {
        // 
        apiUrl = 'http://www.b2c.com/index.php/api.html'
    } else if (process.env.NODE_ENV === 'production') {
        // 
        if (!window.apiUrl) common.errorToBack('!')
        apiUrl = window.apiUrl
    }
    return apiUrl
}

// token
function post (method, data, callback, show = true) {
    // token
    if (methodToken.indexOf(method) >= 0) {
        data.token = common.getStorage('user_token')
    }
    data.method = method
    sendPost(ApiUrl(), qs.stringify(data), {}, callback)
}

// 
function uploadFile (type, param, callback) {
    if (type === 'image') {
        param.append('method', 'images.upload')
    } else if (type === 'video') {
        param.append('method', 'videos.addvideo')
    }
    let config1 = {
        headers: {'Content-Type': 'multipart/form-data'}
    }
    sendPost(ApiUrl(), param, config1, callback)
}

// axios 
function sendPost (url, data, config = {}, callback) {
    Loading.open(Object.keys(config).length ? '...' : '...')
    axios.post(url, data, config).then(response => {
        callback(response.data)
        Loading.close()
        if (!response.data.status) {
            // 
            common.errorToBack(response.data.msg)
            if (response.data.data === 14007 || response.data.data === 14006) {
                // token user_token
                common.removeStorage('user_token')
                // 
                common.jumpToLogin()
            }
        }
    }).catch(err => {
        if (err && err.response) {
            switch (err.response.status) {
            case 400:
                err.message = ''
                break
            case 401:
                err.message = ''
                break
            case 403:
                err.message = ''
                break
            case 404:
                err.message = `: ${err.response.config.url}`
                break
            case 408:
                err.message = ''
                break
            case 500:
                err.message = ''
                break
            case 501:
                err.message = ''
                break
            case 502:
                err.message = ''
                break
            case 503:
                err.message = ''
                break
            case 504:
                err.message = ''
                break
            case 505:
                err.message = 'HTTP'
                break
            default:
                break
            }
            Loading.close()
            common.errorToBack(err.message)
        }
    })
}

