<template>
    <div>
        <yd-cell-group>
            <yd-cell-item v-if="showType">
                <span slot="left"></span>
                <yd-input slot="right" v-model="fullName" placeholder="" readonly disabled></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" v-model="cardCode" ref="code" regex="bankcard" placeholder=""></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" v-model="name" :on-focus="checkCard" placeholder=""></yd-input>
            </yd-cell-item>
            <yd-cell-item arrow>
                <span slot="left"></span>
                <input slot="right" type="text" @click.stop="show=true" v-model="area" readonly placeholder="">
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" v-model="accountBank" placeholder=""></yd-input>
            </yd-cell-item>
            <yd-cell-item type="label">
                <div slot="left"></div>
                <yd-switch slot="right" v-model="isDefault"></yd-switch>
            </yd-cell-item>
        </yd-cell-group>
        <yd-cityselect v-model="show" :callback="result" :items="district"></yd-cityselect>
        <yd-button size="large" type="danger" @click.native="submitHandler" style="height: .8rem;width: 80%;margin: 0 auto;"></yd-button>
    </div>
</template>

<script>
import District from 'ydui-district/dist/gov_province_city_area_id'
export default {
    data () {
        return {
            show: false,
            area: '',
            name: '', // 
            cardCode: '', // 
            showType: false, // 
            bankName: '', // 
            cardType: '', // 
            cardTypeName: '', // 
            bankCode: '', // 
            areaId: '', // id,
            accountBank: '', // 
            isDefault: true, // 
            district: District
        }
    },
    mounted () {
        // input  
        this.$refs.code.setFocus()
    },
    computed: {
        //  
        fullName () {
            return this.bankName + this.cardTypeName
        }
    },
    methods: {
        // /id
        result (ret) {
            this.areaId = ret.itemValue3
            this.area = ret.itemName1 + ' ' + ret.itemName2 + ' ' + ret.itemName3
        },
        // 
        submitHandler () {
            if (!this.cardCode) {
                this.$dialog.toast({mes: '', timeout: 1000})
            } else if (!this.bankName && !this.cardType && !this.bankCode) {
                this.$dialog.toast({mes: '', timeout: 1000})
            } else if (!/^[\u4E00-\u9FA5]{2,4}$/.test(this.name)) {
                this.$dialog.toast({mes: '', timeout: 1000})
            } else if (!this.areaId) {
                this.$dialog.toast({mes: '', timeout: 1000})
            } else if (!this.accountBank) {
                this.$dialog.toast({mes: '', timeout: 1000})
            } else {
                this.$api.addBankCard({
                    bankName: this.bankName,
                    areaId: this.areaId,
                    accountBank: this.accountBank,
                    accountName: this.name,
                    bankCode: this.bankCode,
                    cardNumber: this.cardCode,
                    cardType: this.cardType,
                    isDefault: this.isDefault ? 1 : 2
                }, res => {
                    if (res.status) {
                        this.$dialog.toast({
                            mes: res.msg,
                            timeout: 1000,
                            callback: () => {
                                this.$router.go(-1)
                            }
                        })
                    }
                })
            }
        },
        // inputapi
        checkCard () {
            this.$api.getBankCardOrganization({card_code: this.cardCode}, res => {
                if (res.status) {
                    const _data = res.data
                    this.bankName = _data.name
                    this.cardType = _data.type
                    this.bankCode = _data.bank_code
                    this.cardTypeName = _data.type_name
                    this.showType = true
                } else {
                    this.bankName = this.cardType = this.bankCode = this.cardTypeName = ''
                    this.showType = false
                }
            })
        }
    }
}
</script>

<style scoped>

</style>



