<template>
    <div class="login">
        <div class="login-img">
            <img src="../../static/image/group14.png"/>
        </div>
        <!--<yd-cell-group>-->
        <yd-tab v-model="tab" :prevent-default="false" :item-click="itemClick">
            <yd-tab-panel label="">
                <div class="login-content">
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" ref="tel" required :show-success-icon="false" regex="mobile" v-model="mobile" placeholder=""></yd-input>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" type="password" v-model="password" placeholder=""></yd-input>
                    </yd-cell-item>
                    <yd-cell-item v-if="isShowCaptcha">
                        <span class="w4" slot="left"></span>
                        <yd-input slot="right" type="text" v-model="captcha" placeholder=""></yd-input>
                        <img slot="right" :src="localCaptcha" alt="" @click="reloadCaptcha" width="150">
                    </yd-cell-item>
                </div>
            </yd-tab-panel>
            <yd-tab-panel label="">
                <div class="login-content">
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input type="text" slot="right" ref="tel" required :show-success-icon="false" regex="mobile" v-model="mobile" placeholder=""></yd-input>
                        <yd-sendcode slot="right"
                                     storage-key="login"
                                     v-model="countDown"
                                     init-str=""
                                     @click.native="sendCode"
                                     type="primary"
                        ></yd-sendcode>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left"></span>
                        <yd-input slot="right" v-model="code" placeholder=""></yd-input>
                    </yd-cell-item>
                </div>
            </yd-tab-panel>
            <yd-button size="large" type="danger" @click.native="login"></yd-button>
        </yd-tab>
        <!--</yd-cell-group>-->
        ?<span style="color: #10aeff;" @click="toRegister">&nbsp;</span>
    </div>
</template>

<script>
export default {
    data () {
        return {
            tab: parseInt(this.GLOBAL.getStorage('loginType')) || 0,
            pid: '', // 
            mobile: null,
            password: null,
            code: null, // 
            isShowCaptcha: false, // 
            captcha: '', // 
            localCaptcha: this.GLOBAL.getCaptcha(), // 
            countDown: false //  true
        }
    },
    created () {
        if (this.GLOBAL.getStorage('user_token')) {
            this.$dialog.toast({
                mes: '!',
                timeout: 2000,
                callback: () => {
                    this.$router.go(-1)
                }
            })
        }
        // localStoragepid
        if (this.GLOBAL.getStorage('pid')) {
            // pid24
            // 24  pid
            //  this.pid
            if (parseInt(this.GLOBAL.getStorage('time')) < new Date().getTime()) {
                this.GLOBAL.removeStorage('pid')
                this.GLOBAL.removeStorage('time')
            } else {
                this.pid = this.GLOBAL.getStorage('pid')
            }
        }
    },
    computed: {
        // 
        rightMobile () {
            let res = {}
            if (!this.mobile) {
                res.status = false
                res.msg = ''
            } else if (!(/^1[345678]{1}\d{9}$/gi.test(this.mobile))) {
                res.status = false
                res.msg = ''
            } else {
                res.status = true
            }
            return res
        }
    },
    methods: {
        //   tab  
        itemClick (key) {
            this.tab = key
            this.GLOBAL.setStorage('loginType', key)
        },
        // 
        sendCode () {
            if (!this.rightMobile.status) {
                this.$dialog.toast({mes: this.rightMobile.msg, timeout: 1000})
            } else {
                this.$dialog.loading.open('...')
                setTimeout(() => {
                    this.$dialog.loading.close()
                    this.$api.sms({mobile: this.mobile, code: 'login'}, res => {
                        if (res.status) {
                            this.countDown = true
                            this.$dialog.toast({mes: res.msg, icon: 'success', timeout: 1000})
                        }
                    })
                }, 1000)
            }
        },
        // 
        login () {
            if (!this.rightMobile.status) {
                this.$dialog.toast({mes: this.rightMobile.msg, timeout: 1000})
            } else {
                // 
                if (!this.tab) {
                    if (!this.password) {
                        this.$dialog.toast({mes: '!', timeout: 1000})
                    } else {
                        this.$api.login({mobile: this.mobile, password: this.password}, res => {
                            if (res.status) {
                                this.GLOBAL.setStorage('user_token', res.data)
                                this.redirectHandler()
                            } else {
                                // 
                                if (res.data === 10013) {
                                    this.isShowCaptcha = true
                                }
                                // 
                                if (this.isShowCaptcha) {
                                    this.localCaptcha = this.GLOBAL.getCaptcha()
                                }
                            }
                        })
                    }
                } else {
                    // 
                    if (!this.code) {
                        this.$dialog.toast({mes: '!', timeout: 1000})
                    } else {
                        let data = {mobile: this.mobile, code: this.code}
                        if (this.pid) {
                            data['pid'] = this.pid
                        }
                        this.$api.smsLogin(data, res => {
                            if (res.status) {
                                //   
                                this.GLOBAL.removeStorage('pid')
                                this.GLOBAL.removeStorage('time')
                                this.GLOBAL.setStorage('user_token', res.data)
                                this.redirectHandler()
                            }
                        })
                    }
                }
            }
        },
        //  
        redirectHandler () {
            this.$router.replace(this.$route.query.redirect ? decodeURIComponent(this.$route.query.redirect) : '/')
        },
        // 
        toRegister () {
            if (this.$route.query.redirect) {
                this.$router.replace({path: '/register', query: {redirect: decodeURIComponent(this.$route.query.redirect)}})
            } else {
                this.$router.replace({path: '/register'})
            }
        },
        // 
        reloadCaptcha () {
            this.localCaptcha = this.GLOBAL.getCaptcha()
        }
    }
}
</script>

<style>
    .login .login-img{
        margin: .5rem auto;
    }
    .login .login-img img{
        border-radius: 50%;
    }
    .login .yd-tab-panel{
        background-color: #f5f5f5;
    }
    .login .yd-tab-panel-item .login-content{
        background-color: #fff;
    }
    .login .yd-tab-nav .yd-tab-nav-item:not(:last-child):after{
        border: none;
    }
    .login .yd-btn-block{
        /*background-color: #ff43;*/
       height: .8rem;
       width: 90%;
       margin: .5rem auto;
    }
</style>



