<template>
    <div class="ordercell">
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right" class="demo-list-price">{{ this.GLOBAL.formatMoney(goods_amount, 2, '') }}</span>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right" class="demo-list-price">- {{ this.GLOBAL.formatMoney(goods_pmt, 2, '') }}</span>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right" class="demo-list-price">- {{ this.GLOBAL.formatMoney(order_pmt, 2, '') }}</span>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right" class="demo-list-price">+ {{ this.GLOBAL.formatMoney(cost_freight, 2, '') }}</span>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right" class="demo-list-price">- {{ this.GLOBAL.formatMoney(coupon_pmt, 2, '') }}</span>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right" class="demo-list-price">{{ this.GLOBAL.formatMoney(amount, 2, '') }}</span>
            </yd-cell-item>
        </yd-cell-group>
    </div>
</template>

<script>
export default {
    props: {
        // 
        goods_amount: {
            type: [Number, String],
            require: true
        },
        // 
        cost_freight: {
            type: [Number, String],
            require: true
        },
        // /
        amount: {
            type: [Number, String],
            require: true
        },
        // 
        goods_pmt: {
            type: [Number, String],
            require: true
        },
        order_pmt: {
            type: [Number, String],
            require: true
        },
        coupon_pmt: {
            type: [Number, String],
            require: false
        }
    }
}
</script>

<style>
</style>



