<template>
    <div class="aftersalesdetail">
        <div class="orderdetailheader">
            <div class="">
                <h3>{{ typeName }}</h3>
                <p>{{ statusName }}  {{ refundName }} {{ reshipName }}</p>
            </div>
        </div>
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right">{{ aftersales_id }}</span>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right">{{ typeName }}</span>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <span slot="right" class="demo-list-price">{{ refund }}</span>
            </yd-cell-item>
        </yd-cell-group>
        <yd-cell-group title="" v-if="type === 2">
            <yd-list theme="4">
                <yd-list-item v-for="(item, index) in items" :key="index">
                    <img slot="img" :src="item.image_url">
                    <h3 class="goodsname" slot="title">{{ item.name }}</h3>
                    <p class="goods" slot="title">{{ item.addon }}</p>
                    <yd-list-other slot="other">
                        <div>x{{ item.nums }}</div>
                    </yd-list-other>
                </yd-list-item>
            </yd-list>
        </yd-cell-group>
        <yd-cell-group title="" v-if="images">
            <div class="afterserviceimg">
                <img class="thumbnail-list" :src="item.url" alt="" v-for="(item, index) in images" :key="index">
            </div>
        </yd-cell-group>
        <yd-cell-group title="" v-if="reason">
            <yd-cell-item>
                <yd-textarea v-model="reason" slot="right" readonly  :show-counter="false"></yd-textarea>
            </yd-cell-item>
        </yd-cell-group>
        <!--   -->
        <div v-if="status === 2 && reshipStatus === 1">
            <h4></h4>
            <p>{{ reship.reship_name }}</p>
            <p>{{ reship.reship_mobile }}</p>
            <p>{{ reship.reship_area }} {{ reship.reship_address }}</p>
            :
            <select @change="selected">
                <option :value="item.code" v-for="(item, index) in express" :key="index">{{ item.name }}</option>
            </select>
            :<input type="text" v-model="logisticsCode">
        <yd-button size="large" type="danger" @click.native="send"></yd-button>
        </div>
        <!--   -->
        <div v-if="status === 2 && reshipStatus > 1">
            <span>:{{ logistics }}</span>
            <span>:{{ logisticsCode }}</span>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            aftersales_id: this.$route.query.aftersales_id || 0, // id
            items: [], // 
            type: '', //  
            typeName: '', // 
            status: '', // 
            statusName: '', // 
            reason: '', // 
            mark: '', // 
            images: [], // 
            refund: '', // 
            refundName: '', // 
            reship_id: '', // 
            reshipStatus: '', // 
            reshipName: '', // 
            reship: [], // 
            express: [
                {name: '', code: 'sf'},
                {name: '', code: 'sto'},
                {name: '', code: 'yt'},
                {name: '', code: 'zto'},
                {name: '', code: 'ht'},
                {name: '', code: 'yd'}
            ],
            logistics: '', // 
            logisticsCode: '' // 
        }
    },
    created () {
        this.afterSalesDetail()
    },
    methods: {
        afterSalesDetail () {
            this.$api.afterSalesInfo({
                aftersales_id: this.aftersales_id
            }, res => {
                if (res.status) {
                    let info = res.data.info
                    this.reason = info.reason // 
                    this.refund = info.refund // 
                    this.images = info.images // 
                    this.mark = info.mark
                    // type
                    if (info.type === 1) {
                        this.type = 1
                        this.typeName = ''
                    } else {
                        this.type = 2
                        this.typeName = ''
                        this.items = info.items // 
                        if (res.data.reship) {
                            this.reship = res.data.reship
                        }
                    }
                    // 
                    if (info.status === 1) {
                        this.status = 1
                        this.statusName = ''
                    } else if (info.status === 2) {
                        this.status = 2
                        this.statusName = ''
                        // 
                        if (info.bill_refund) {
                            if (info.bill_refund.status === 1) {
                                this.refundName = ''
                            } else if (info.bill_refund.status === 2) {
                                this.refundName = ''
                            }
                        }
                        // 
                        if (info.bill_reship) {
                            this.reship_id = info.bill_reship.reship_id
                            if (info.bill_reship.status === 1) {
                                this.reshipStatus = 1
                                this.reshipName = ''
                            } else if (info.bill_reship.status === 2) {
                                this.reshipStatus = 2
                                this.reshipName = ''
                                this.logistics = info.bill_reship.logi_code
                                this.logisticsCode = info.bill_reship.logi_no
                            } else {
                                this.reshipStatus = 3
                                this.reshipName = ''
                                this.logistics = info.bill_reship.logi_code
                                this.logisticsCode = info.bill_reship.logi_no
                            }
                        }
                    } else {
                        this.statusName = ''
                    }
                }
            })
        },
        selected (e) {
            this.logistics = e.target.value
        },
        // 
        send () {
            this.$api.sendShip({
                reship_id: this.reship_id,
                logi_code: this.logistics,
                logi_no: this.logisticsCode
            }, res => {
                if (res.status) {
                    this.afterSalesDetail()
                }
            })
        }
    }
}
</script>

<style scoped>
    .orderdetailheader{
        width: 100%;
        height: 2.3rem;
        background: url(../../static/image/backimg.png) no-repeat;
        background-size: 100% 100%;
    }
    .orderdetailheader>div{
        width: 70%;
        margin: 0 auto;
        text-align: left;
        color: #fff;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }
    .orderdetailheader h3{
        font-size: .3rem;
        margin-bottom: .2rem;
    }
</style>



