<template>
    <div class="goodstitle">
        <div class="goodsheader">
            <div class="goodsheader-left">
                <h3 class="goodsname">{{ product.name }}</h3>
                <p>{{ brief }}</p>
            </div>
            <yd-button class="share" @click.native="show1 = true">
                <img src="../../static/image/share.png"/>
                <p></p>
            </yd-button>
        </div>
        <div class="price-salesvolume">
            <div class="price">
                <h2>{{ this.GLOBAL.formatMoney(product.price, 2, '') }}</h2>
                <p>{{ product.mktprice }}</p>
            </div>
            <div class="salesvolume">
                <p>{{ buyCount }} {{ unit }}</p>
            </div>
        </div>
        <yd-popup v-model="show1" position="center" width="90%">
            <div style="background-color:#fff;padding: 20px;">
                <share :config="config"></share>
            </div>
        </yd-popup>
    </div>
</template>

<script>
export default {
    props: {
        config: 'config',
        product: {
            type: [Array, Object],
            default () {
                return []
            }
        },
        brief: {
            type: String
        },
        buyCount: {
            type: Number
        },
        unit: {
            type: String
        }
    },
    data () {
        return {
            show1: false,
            url: '', //  window.location.href
            source: '', // QQ, head<meta name="site" content="http://overtrue" />
            title: '', //  document.title  <meta name="title" content="share.js" />
            description: '', // , head<meta name="description" content="PHP" />
            image: '', // , img
            sites: ['qzone', 'qq', 'weibo', 'wechat', 'douban'], // 
            disabled: ['google', 'facebook', 'twitter'], // 
            wechatQrcodeTitle: '', // 
            wechatQrcodeHelper: '<p></p><p></p>'
        }
    }
}
</script>

<style>
    .goodsheader .yd-btn-primary:active{
        background-color: #fff;
    }
    a.icon-google, a.icon-twitter, a.icon-facebook, a.icon-diandian{
        display: none !important;
    }
</style>



